html {
  font-family: $libra;
  // font-family: $gotham;
  font-weight: $norm;
  font-size: 1rem;
  color: $black;
  @include bp(desktop) {
    font-size: 0.8rem;
  }
  @include bp(M) {
    font-size: 0.7rem;
  }
  @include bp(S) {
    font-size: 0.55rem;
  }
}
.headline {
  .elementor-heading-title {
    font-family: $libra;
    font-size: rem(58px);
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: rem(82px);
    letter-spacing: 0.29px;
    text-align: left;
    color: #050303;
  }
}

.sub-headline {
  .elementor-heading-title {
    font-family: $libra;
    font-size: rem(30px);
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: rem(42px);
    letter-spacing: 0.06px;
    text-align: left;
    color: #fff;
  }
}
.faq-title {
  font-family: $libra;
  font-size: rem(30px);
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: rem(42px);
  letter-spacing: 0.06px;
  color: $cta-accent;
}

.title-text {
  .elementor-heading-title {
    font-family: $lEX;
    font-size: rem(20px);
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: rem(28px);
    letter-spacing: rem(3px);
    color: white;
    text-transform: uppercase;
  }
}

.headline-03 {
  .elementor-heading-title {
    font-family: $lEX;
    font-size: rem(28px);
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: rem(24px);
    letter-spacing: rem(4.2px);
    text-align: left;
    color: #b32e26;
    text-transform: uppercase;
  }
}

p.elementor-heading-title,
p {
  font-family: $lEX;
  font-size: rem(18px);
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: rem(29px);
  letter-spacing: -0.18px;
  text-align: left;
  color: #252525;
}

.nav-button {
  font-family: $lEX;
  text-transform: uppercase;
  font-size: rem(14px);
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: rem(24px);
  letter-spacing: rem(2.1px);
  text-align: left;
  color: #050303;
}

.eyebrow {
  &:not(.no-border) .elementor-heading-title {
    &:after {
      content: "";
      display: block;
      margin-top: rem(10px);
      border-bottom: 1px solid $cta-accent;
    }
  }
  .elementor-heading-title {
    font-family: $lEX;
    text-transform: uppercase;
    color: $cta-accent;
    font-size: rem(16px);
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: rem(24px);
    letter-spacing: rem(2.4px);
  }
}

.eyebrow-light .elementor-heading-title {
  font-family: $lEX;
  font-size: rem(16px);
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: rem(30px);
  letter-spacing: 2.4px;
  text-align: left;
  color: #292929;
  text-transform: uppercase;
}
.eyebrow-med .elementor-heading-title {
  font-family: $lEX;
  font-size: rem(20px);
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: rem(25px);
  letter-spacing: 3px;
  text-align: left;
  color: #292929;
  text-transform: uppercase;
}

