.block-cta {
  display: flex;
  .elementor-button {
    @extend .nav-button;
    text-align: left;
    height: rem(55px);
    display: flex;
    align-items: center;
    justify-content: center;
    min-width: 220px;
    background-color: $cta-accent;
    color: white;
    padding: rem(10px) rem(30px);
    border-radius: 0px;
    .elementor-align-icon-right {
      margin-left: rem(20px);
    }
    &:hover {
      background-color: white;
      border: 1px solid $cta-accent;
      color: $cta-accent;
    }
    @include bp(M) {
      height: $button-height-small;
      min-width: 154px;
    }
  }
}
.static-cta .elementor-button {
  color: $cta-accent;
  font-family: $lEX;
  font-size: rem(16px);
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: rem(29px);
  letter-spacing: -0.16px;
  text-align: left;
  text-transform: uppercase;
  background-color: transparent;
  overflow: hidden;
  &:after {
    content: "";
    border-bottom: 1px solid #b32e26;
    width: 100%;
    display: block;
    border-bottom: 1px solid $cta-accent;
    transition: $hover;
  }
  &:hover {
    &:after {
      transform: translateX(85%);
    }
  }
  i {
    transform: rotate(45deg);
  }
}

.block-cta.cta-no-bg .elementor-button {
  background-color: none;
}
.block-cta-white .elementor-button {
  color: $cta-accent;
  background-color: white;
  &:hover {
    color: white;
    background-color: $cta-accent;
    border: 1px solid white;
  }
}

